import { t } from 'i18next';
import { memo, useEffect, useRef } from 'react';
import { Trans } from 'react-i18next';
import NoData from '../../../components/NoData/NoData';
import { useUserDetailQuery } from '../../../queries/authQuery';
import { conversionDate } from '../../../utils/moment';
import DetailItem from './DetailItem/DetailItem';

interface LearnerDetailProps {
	activeUser: number;
}

const LearnerDetail = ({ activeUser }: LearnerDetailProps) => {
	const ref = useRef<HTMLDivElement>(null);

	const userDetail = useUserDetailQuery(activeUser);
	const item = userDetail.item;

	useEffect(() => {
		ref.current?.scrollTo({ top: 0 });
	}, [activeUser]);

	return (
		<div className='w-[1025px] overflow-auto scroll-none' ref={ref}>
			{activeUser ? (
				<div className='px-10 py-10'>
					<p className='mb-12 text-2xl font-bold text-gray-700'>{t(' ')}</p>
					<div className='mb-12'>
						<p className='mb-2 text-base font-bold text-gray-700'>{t(' ')}</p>
						<div className='flex gap-10'>
							<div className='flex flex-col w-full'>
								<DetailItem title={t(' ID')} content={item?.users.login_id || '-'} />
								<DetailItem title={t(' ')} content={item?.user_profiles.friend_code || '-'} />
								<DetailItem title={t(' ')} content={item?.users.learner_name || '-'} />
								<DetailItem
									title={t(' ')}
									content={
										!!item?.user_profiles.create_time &&
										conversionDate(item?.user_profiles.create_time, 'YYYY-MM-DD HH:mm')
									}
								/>
							</div>
							<div className='flex flex-col w-full'>
								<DetailItem title={t('')} content={item?.user_profiles.character_name || '-'} />
								<DetailItem title={t(' ')} content={item?.user_profiles.user_family_name || '-'} />
								<DetailItem title={t(' ')} content={item?.user_profiles.user_gender || '-'} />
								<DetailItem title={t('  ')} content={item?.user_profiles.best_ranking_point || 0} />
							</div>
						</div>
					</div>
					<p className='mb-2 text-base font-bold text-gray-700'>{t('')}</p>
					<div className='flex gap-10'>
						<div className='flex flex-col w-full'>
							<DetailItem title={t('   ')} content={item?.achievements.player_sr_count || 0} />
							<DetailItem title={t(' ')} content={`${item?.achievements.player_count_login || 0} days`} />
							<DetailItem title={t(' ')} content={`Level ${item?.achievements.player_level_up || 0}`} />
							<DetailItem title={t('   ')} content={item?.achievements.quest_count_clear || 0} />
							<DetailItem title={t('  ')} content={item?.achievements.tt_count_clear || 0} />
							<DetailItem title={t('  Good  ')} content={item?.achievements.tt_sr_count_good || 0} />
							<DetailItem title={t('  Nice  ')} content={item?.achievements.tt_sr_count_nice || 0} />
							<DetailItem
								title={t('  Great  ')}
								content={item?.achievements.tt_sr_count_great || 0}
							/>
							<DetailItem
								title={t('  Perfect  ')}
								content={item?.achievements.tt_sr_count_perfect || 0}
							/>
							<DetailItem
								title={t('  Fantastic  ')}
								content={item?.achievements.tt_sr_count_fantastic || 0}
							/>
							<DetailItem title={t('  ')} content={item?.achievements.battle_map_count_clear || 0} />
						</div>

						<div className='flex flex-col w-full'>
							<DetailItem
								title={t('   ')}
								content={item?.achievements.projector_count_clear || 0}
							/>
							<DetailItem
								title={t('   ')}
								content={item?.achievements.npc_likability_count_talk || 0}
							/>
							<DetailItem title={t('   ')} content={item?.achievements.wordie_count_complete || 0} />
							<DetailItem title={t('    ')} content={item?.achievements.chunk_card_count_sr || 0} />
							<DetailItem
								title={t('    ')}
								content={item?.achievements.chunk_card_count_level_up || 0}
							/>
							<DetailItem
								title={t('   ')}
								content={item?.achievements.landmark_count_level_up || 0}
							/>
							<DetailItem title={t('  ')} content={item?.achievements.landmark_count_collect || 0} />
							<DetailItem title={t('  ')} content={item?.achievements.battle_map_count_clear || 0} />
							<DetailItem
								title={<Trans i18nKey={'  (   )'} />}
								content={item?.achievements.linker_battle_count_clear || 0}
							/>
							<DetailItem title={t('  ')} content={`${item?.achievements.lumy_count_get || 0} Lumy`} />
							<DetailItem
								title={t('   ')}
								content={item?.achievements.daily_mission_count_study_point || 0}
							/>
						</div>
					</div>
				</div>
			) : (
				<NoData image='non_actived_learner' label='    .' />
			)}
		</div>
	);
};

export default memo(
	LearnerDetail,
	(prev: LearnerDetailProps, next: LearnerDetailProps) => prev.activeUser === next.activeUser
);
