import { t } from 'i18next';
import useToast from '../stores/useToast';

const useNoticeToast = () => {
	const { openToast, closeToast } = useToast();

	const handleOpenSizeToast = () => {
		openToast({
			title: 'Image Upload Failed',
			description: (
				<>
					<p>{t('   500KB .')}</p>
					<p>{t('500KB    .')}</p>
				</>
			),
			rightButton: { text: t(''), onClick: () => closeToast() },
			isClose: true,
			leftIcon: 'toast_warning',
			position: 'center',
		});
	};

	const handleOpenDimensionToast = () => {
		openToast({
			title: 'Image Upload Failed',
			description: (
				<>
					<p>{t('   800x250 .')}</p>
					<p>{t('   .')}</p>
				</>
			),
			rightButton: { text: t(''), onClick: () => closeToast() },
			isClose: true,
			leftIcon: 'toast_warning',
			position: 'center',
		});
	};

	const handleOpenFormatToast = () => {
		openToast({
			title: 'Image Upload Failed',
			description: (
				<>
					<p>{t('    .')}</p>
					<p>{t('   .')}</p>
				</>
			),
			rightButton: { text: t(''), onClick: () => closeToast() },
			isClose: true,
			leftIcon: 'toast_warning',
			position: 'center',
		});
	};

	return { handleOpenSizeToast, handleOpenDimensionToast, handleOpenFormatToast };
};

export default useNoticeToast;
